@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

body {
  background: #fff;
  color: #222;
  font-family: 'Roboto', 'Yu Gothic', 'Meiryo', sans-serif;
  transition: background 0.2s, color 0.2s;
}
body.dark-mode {
  background: #181818;
  color: #eee;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 2rem 1rem 1rem;
  background: rgba(255,255,255,0.85);
  border-bottom: 1px solid #e0e0e0;
  position: sticky;
  top: 0;
  z-index: 100;
  box-shadow: 0 2px 8px rgba(0,0,0,0.03);
}
body.dark-mode .header {
  background: rgba(24,24,24,0.95);
  border-bottom: 1px solid #333;
}

.logo {
  font-size: 1.5rem;
  font-weight: bold;
  color: #646cff;
  letter-spacing: 0.05em;
  padding: 0;
  height: auto;
}

.darkmode-toggle {
  background: linear-gradient(90deg, #646cff 0%, #61dafb 100%);
  color: #fff;
  border: none;
  border-radius: 2em;
  padding: 0.5em 1.5em;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(100,108,255,0.08);
  transition: background 0.2s, color 0.2s, box-shadow 0.2s;
  outline: none;
}
.darkmode-toggle.active {
  background: linear-gradient(90deg, #222 0%, #444 100%);
  color: #ffe066;
  box-shadow: 0 2px 12px rgba(34,34,34,0.15);
}
.darkmode-toggle:hover {
  filter: brightness(1.1);
}

#root {
  max-width: 1280px;
  margin: 0 auto;
  padding: 2rem;
  text-align: center;
}

.logo {
  height: 6em;
  padding: 1.5em;
  will-change: filter;
  transition: filter 300ms;
}
.logo:hover {
  filter: drop-shadow(0 0 2em #646cffaa);
}
.logo.react:hover {
  filter: drop-shadow(0 0 2em #61dafbaa);
}

@keyframes logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (prefers-reduced-motion: no-preference) {
  a:nth-of-type(2) .logo {
    animation: logo-spin infinite 20s linear;
  }
}

.card {
  padding: 2em;
}

.read-the-docs {
  color: #888;
}
